<template>
  <v-card>
    <v-row>
      <v-col>
        <v-data-table
          disable-pagination
          disable-filtering
          dense
          hide-default-footer
          :headers="headers"
          :items="characteristics"
          :item-class="item_class"
          class="tabela"
          :loading="loading"
          no-data-text="Nenhuma informação encontrada"
          loading-text="Carregando"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <v-toolbar-title>Características</v-toolbar-title>
            </v-toolbar>
          </template>
        </v-data-table>
      </v-col>
      <v-col>
        <v-data-table
          disable-pagination
          disable-filtering
          dense
          hide-default-footer
          :headers="problemHeaders"
          :items="problems"
          class="tabela"
          :loading="loading"
          no-data-text="Nenhuma informação encontrada"
          loading-text="Carregando"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <v-toolbar-title>Detalhes dos Problemas</v-toolbar-title>
            </v-toolbar>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import helper from "@/mixins/helper";

export default {
  name: "InformaçõesHorário",
  props: ["idHorario"],
  mixins: [helper],
  data() {
    return {
      headers: [
        { text: "Nome", value: "desc_metrica" },
        { text: "Valor", value: "valor" },
        { text: "Peso", value: "peso" },
        { text: "Pontuação", value: "valor_multiplicado" },
        { text: "Tipo métrica", value: "tipo_metrica" },
      ],
      problemHeaders: [
        { text: "Tipo", value: "desc_metrica" },
        { text: "Professor", value: "nome_professor" },
        { text: "Sala", value: "nome_sala" },
        { text: "Turno", value: "nome_turno" },
        { text: "Detalhes", value: "detalhes" },
      ],
      characteristics: [],
      problems: [],
      constants: {
        CONFLITOS: 1,
        RESTRICOES: 2,
        RESTRICOES_OPCIONAIS: 3,
        ZERO_DUPLAS: 8,
        ESPACOS_JANELAS: 14,
        PREFERENCIAS_NAO_RESOLVIDAS: 15,
        REGIOES: 16,
        DIAS_TRABALHO_EXCESSOS: 20,
      },
      loading: false,
    };
  },

  mounted() {
    this.load();
  },
  methods: {
    async load() {
      try {
        this.loading = true;
        const resp = await this.$axios.get("/horario/caracteristicas", {
          params: {
            id_horario: this.idHorario,
          },
        });
        if (resp.status === 200) {
          this.characteristics = resp.data.caracteristicas;
          this.problems = resp.data.problemas;
        }
      } catch (e) {
        console.error(e);
      } finally {
        this.loading = false;
      }
    },
    item_class(item) {
      if (item.obrigatorio === 1 && item.valor > 0) {
        return "problem";
      }
      return null;
    },
  },

  watch: {
    idHorario() {
      this.load();
    },
  },
};
</script>

<style scoped>
.tabela:deep() .problem > * {
  background: rgb(255, 95, 95);
}
</style>
